<template>
  <!---->
  <dl class="fix">
    <dt class="f_left">
      
    </dt>
    <dd>
      <div class="text_input">
        <input type="text" v-model="inputValue" :maxlength="comment_max_length" @focus="onfocus">
      </div>
      <div v-if="controlVis">
        <span class="num">{{inputLength}} / {{comment_max_length}}</span>
        <span class="">
            <el-button type="success" class="f_right" size="large" @click.native="onsubmit" :loading="loadingFlag">{{buttonText}}</el-button>
          </span>
      </div>
    </dd>
  </dl>

</template>

<script>
  import {COMMENT_MAX_LENGTH_INSIDE} from '../../../common/js/config';
  export default  {
    created() {

    },
    props: {
      hasImg: {
        type: Boolean,
        default: false
      },
      loadingFlag: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        buttonText: '',
        inputValue: '',
        inputLength: 0,
        comment_max_length: COMMENT_MAX_LENGTH_INSIDE,
        controlVis: false
      }
    },
    computed: {
    },
    watch: {
      // 
      inputValue(newValue) {
        this.inputLength = newValue.length;
      },
      //load
      loadingFlag(newValue) {

        if(newValue) {
          this.buttonText = ''
        }else{
          this.buttonText = ''
        }
      }
    },
    methods: {
      //
      onfocus() {
        this.controlVis = true;
      },
      //onSubmit
      onsubmit() {
        if(this.inputValue.length == 0) {
          this.$message({
            type: 'warning',
            message: ''
          })
          return;
        }
        this.$emit('onSubmit', this.inputValue);
        this.$emit('standbySubmit');
      },
      clearInput() {
        this.inputValue = '';
        this.controlVis = false;
      }
    }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import "../../../common/scss/common";
  @import "../../../common/scss/ui";
  dl{
    dt{
      line-height: 41px;
      img{
        width:40px;
        height:40px;
        border-radius: 50%;
      }
    }
    dd{
      padding-left: 30px;
      .text_input{
        width:100%;
        margin-bottom: 10px;
        input{
          @include inputStyle;
          height:40px;
        }
      }
      &>div{

      }
      input{

      }
      .num{
        color:$color-text-d;
      }
    }
  }
</style>



